import React from 'react'
import OverallRating from './OverallRating'
import { Link } from 'react-router-dom'
import { useParams } from 'react-router-dom'
// import KudosAndAppreciation from './KudosAndAppreciation'
// import Feedback from './Feedback'
import Escalation from './Escalation'
const ViewRating = () => {
    const {ratingName}=useParams()
  return (
    <div className="ticketScoreSection">
                <div className="cardBox myRequest mb-3">
                    <div className="cardHead d-flex justify-content-between">
                        <div className="d-flex align-items-center">
                            <img src="images/kpi-guidline.svg" alt="" width={40} className="me-2" />
                            <ul className="breadCrumb">
                                <li>
                                    <Link to={"/rating-review"}>
                                    <span>Rating &amp; Review</span> &gt;{" "}
                                    </Link>
                                </li>
                                <li className="active border-0">{ratingName}</li>
                            </ul>
                        </div>
                        <div className="selectDate">
                            <select
                                className="form-select form-select-sm"
                                aria-label="Small select example"
                            >
                                <option selected="">Select Financial year</option>
                                <option value={1}>April-22 To March-23</option>
                                <option value={2}>April-23 To March-24</option>
                            </select>
                        </div>
                    </div>
                
                {/* {ratingName ==="rating-feedback" &&<Feedback/>} */}
                {ratingName ==="escalation" &&<Escalation/>}
                </div>
            </div>
  )
}

export default ViewRating
